<template>
  <div class="report">
    <my-modal-box ref="msg">
      <IdentDetail />
    </my-modal-box>
    <Header />
    <div class="reportBody">
      <div class="reportBodyClose" @click="handleReportBack()">×</div>
      <div class="reportBodyMain">
        <div class="reportText">
          <div
            v-if="iframeHide"
            class="reportIframeLoading"
            v-loading="iframeHide"
            element-loading-text="正在解析报告，请稍后！"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.2)"
          ></div>
          <div v-else class="reportIframeContainer">
            <div class="reportIframeTitle">{{ correctInfo.report_name }}</div>
            <iframe
              :src="iframeSrc + '?time=' + new Date().getTime()"
              class="reportIframeContent"
              ref="reportIframe"
              id="reportIframe"
            ></iframe>
          </div>
        </div>
        <div class="reportTextCon" style="border: 0">
          <el-tabs
            v-model="activeName"
            @tab-click="handleClick"
            type="border-card"
            stretch
          >
            <el-tab-pane
              label="法规分析"
              name="first"
              style="margin-top: 0; padding-top: 0"
            >
              <div class="reportContent">
                <Collapse
                  :handleDetailShow="handleDetailShow"
                  :collapseData="collapseData"
                  :iframeScroll="iframeScroll"
                />
                <div style="margin: 20px">
                  <el-button
                    type="primary"
                    size="medium "
                    icon="el-icon-refresh"
                    @click="getCorrentInfo('all')"
                    >更新</el-button
                  >
                  <el-button
                    type="primary"
                    size="medium "
                    icon="el-icon-s-fold"
                    @click="SaveModifyList()"
                    >修改列表</el-button
                  >
                </div>
              </div>
            </el-tab-pane>
            <el-tab-pane label="实体识别" name="second">
              <div class="reportContent">
                <Collapse
                  :handleDetailShow="handleDetailShow"
                  :collapseData="collapseData"
                  :iframeScroll="iframeScroll"
                />
                <div style="margin: 20px">
                  <el-button
                    type="primary"
                    size="medium "
                    icon="el-icon-refresh"
                    @click="getCorrentInfo('all')"
                    >更新</el-button
                  >
                  <el-button
                    type="primary"
                    size="medium "
                    icon="el-icon-s-fold"
                    @click="SaveModifyList()"
                    >修改列表</el-button
                  >
                </div>
              </div>
            </el-tab-pane>
            <!-- <el-tab-pane label="其他识别" name="third">
                <div class = 'reportContent'>
                  <Collapse @handleDetailShow = 'handleDetailShow'/>
                  <div style = 'margin: 20px;'>
                    <el-button size='small' icon="el-icon-refresh">更新</el-button>
                  </div>
                </div>
              </el-tab-pane> -->
          </el-tabs>
        </div>
      </div>
    </div>
    <div class="footerContainer">
      <Footer />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { SERVE_ADDRESS } from "@/config/utils/constants";
export default {
  name: "report",
  components: {
    Header: () => import("@/components/HeaderComponent.vue"),
    Footer: () => import("@/components/FooterComponent.vue"),
    Collapse: () => import("@/components/correctModify/Collapse.vue"),
    IdentDetail: () => import("@/components/correctModify/IdentDetail.vue"),
  },
  data() {
    return {
      activeName: "first",
      iframeHide: false,
      htmlUrl: "",
      lastId: "",
      backroutes: -1,
    };
  },
  computed: {
    ...mapState({
      correctInfo: (state) => state.correct.correctInfo,
      CurrentReport: (state) => state.correct.CurrentReport,
    }),
    //iframe的路径，如果
    iframeSrc() {
      let url = "";
      if (this.correctInfo) {
        let html =
          this.activeName == "first"
            ? this.correctInfo.html_law
            : this.correctInfo.html_city;
        url = `${SERVE_ADDRESS}/static/${html}`;
      }
      console.log(url);
      return url;
    },
    //判断是法律或者实体，返回不同的数据
    collapseData() {
      let data = {};
      if (this.correctInfo) {
        if (this.activeName == "first") {
          data = {
            id: 1,
            data: [
              this.correctInfo.law_stop_list,
              this.correctInfo.law_run_list,
              this.correctInfo.law_reminder_list,
            ],
          };
        } else {
          data = {
            id: 2,
            data: [
              this.correctInfo.mark_address_list,
              this.correctInfo.mark_company_list,
            ],
          };
        }
      }
      return data;
    },
  },
  watch: {
    //iframe路径改变后route不能返回上一页，记录iframe路由的变化次数
    iframeSrc() {
      this.backroutes--;
    },
  },
  methods: {
    ...mapActions({
      getCorrectDetail: "correct/getCorrectDetail",
      getMarkcontentst: "correct/getMarkcontentst",
      submitModifyList: "correct/submitModifyList",
    }),
    //刚进入时触发的，选择的报告的信息
    async getCorrentInfo(clickStatus) {
      this.iframeHide = true;
      let payload = {
        id: this.CurrentReport.id,
        clickStatus,
      };
      await this.getCorrectDetail(payload);
      this.iframeHide = false;
    },
    handleReportBack() {
      this.$router.go(this.backroutes);
    },
    //切选项卡
    handleClick(tab, event) {
      console.log(tab, event);
      console.log(tab.index);
      if (tab.index == 1) {
        if (this.correctInfo.analyze_com_status == "false") {
          console.log(this.correctInfo);
          this.getCorrentInfo("city");
        }
      }
      this.lastId = "";
    },
    //显示置灰页面
    handleDetailShow() {
      this.$nextTick(() => {
        this.$refs.msg.msgPlugin(true);
      });
    },
    //iframe页面的高亮显示
    iframeScroll(id) {
      this.$nextTick().then(() => {
        let iframeWin = this.$refs.reportIframe.contentWindow;
        let irrameDom = iframeWin.document;
        let oDiv = irrameDom.getElementById(id);
        if (this.lastId) {
          let oLastDiv = irrameDom.getElementById(this.lastId);
          oLastDiv.style.background = "#fff";
          oLastDiv.style.color = "#000";
        }
        oDiv.style.background = "#000";
        oDiv.style.color = "#fff";
        oDiv.scrollIntoView({
          behavior: "smooth",
          block: "center",
          inline: "nearest",
        });
        this.lastId = id;
      });
    },
    //修改列表页面显示
    SaveModifyList() {
      this.$store.commit("correct/openModifyPage");
      this.handleDetailShow();
    },
  },
  created() {
    this.getCorrentInfo();
  },
  mounted() {},
};
</script>

<style>
.report {
  width: 100%;
  height: 100%;
  background: #efefef;
}
.reportBody {
  width: 92%;
  height: 74%;
  background: #fff;
  margin: 10px auto;
  box-shadow: 2px 4px 14px #888888;
  position: relative;
}
.reportIframeLoading {
  width: 100%;
  height: 100%;
  font-size: 30px;
}
.reportIframeLoading .el-loading-spinner i {
  color: #fff;
}

.reportIframeLoading .el-loading-spinner .el-loading-text {
  color: #fff;
}
.reportIframeContainer {
  width: 100%;
  height: 100%;
}
.reportIframeTitle {
  line-height: 22px;
  font-size: 16px;
}
.reportIframeContent {
  width: 100%;
  height: calc(100% - 22px);
  border: 0;
}
.reportBodyClose {
  width: 30px;
  line-height: 30px;
  position: absolute;
  top: -10px;
  right: -10px;
  border: 1px solid #ccc;
  border-radius: 50%;
  text-align: center;
  font-size: 20px;
  background: #fff;
  cursor: pointer;
}
.reportBodyMain {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.reportText {
  width: 50%;
  height: 94%;
  margin: 24px 10px;
  border: 1px solid #ccc;
  padding: 10px;
}

.reportTextCon {
  width: 46%;
  height: 98%;
  margin: 10px;
  padding: 20px 0;
}
.reportTextCon .el-tabs {
  height: 100%;
}
.reportTextCon .el-tabs__content {
  height: calc(100% - 40px);
  padding: 15px;
  padding-top: 0px;
  overflow: auto;
}
</style>